#canvas {
  /* background: yellow; */
  display: flex;
  position: absolute;
  left: 0;
  top: 0;
  bottom: 0;
  right: 0;
}
#canvas svg {
  width: 100%;
  height: 100%;
}
#canvas svg.shadow * {
  stroke-width: 0;
  fill: gray;
}
#canvas .figure-container,
#canvas .figure-container .figure {
  pointer-events: none;
  position: absolute;
  width: auto;
  height: auto;
  max-width: none;
  max-height: none;
}
#canvas .figure-container span {
  display: none;
}

#output {
  display: flex;
  flex-wrap: wrap;
}
#config {
  position: absolute;
  top: 0;
  right: 0;
}
#config label {
  display: flex;
  justify-content: space-between;
}
.figure-container {
  width: 250px;
  height: 250px;
  display: flex;
  flex-direction: column;
  flex-flow: column-reverse;
  align-items: center;
  justify-content: center;
  position: relative;
}

.figure-container span {
  position: absolute;
  bottom: 0;
}

.figure {
  max-width: 250px;
  max-height: 250px;
}
svg * {
  stroke: green;
  stroke-width: 1;
  fill: none;
}
svg .dot {
  stroke: none;
  fill: pink;
}